.modalContainer {
}

.modal-img-container {
	display: grid;
	place-items: center;
	height: 10rem;
	width: 10rem;
	margin: 0 auto;

	& img {
		height: 100%;
		width: 100%;
	}
}

.modal-header {
	text-align: center;
	font-size: 3.4rem;
	margin-bottom: 1.5rem;
}

.modal-footer {
	display: flex;
	justify-content: center;
}

.modal-details {
	display: flex;
	font-size: 1.4rem;
	gap: 3rem;
	margin-bottom: 3rem;
	justify-content: center;
	font-style: italic;
}

.modal-detail {
	padding: 1.4rem;
	background-color: var(--color-primary);

	border-radius: 3rem 0rem 3rem 0rem;
}

.modal-detail {
	text-align: center;
}

.decision-modal--footer {
	display: flex;
	justify-content: center;
	gap: 2rem;
}
